            import React from "react";
            import { Link } from "react-router-dom";
            import BodyWrapper from "../../../components/BodyWrapper";
            import NavWrapper from "../../../components/NavWrapper";
            import PageWrapper from "../../../components/PageWrapper";


            //   import { nationalityData } from "../../../data/basicA1";


            function NationalityPage(){

                
            return (

            <BodyWrapper>
                            
                        <NavWrapper link="/beginars" title=" Back to Beginars Lesson"> </NavWrapper>

                    <div className="mt-16">
                    <PageWrapper title="Nationalities in Finnish">
                <h4 className="text-xl font-semibold text-teal-700 mb-6">Minkmaalainen sin olet? (What nationality are you?)</h4>

            <p className="text-gray-600 mb-6">Level: A1 (Beginner) | Goal: Learn to say your nationality and talk about others in Finnish!</p>

            {/* <!-- Warm-Up Section --> */}
                <h2 className="text-2xl font-semibold text-teal-600 mb-3">1. Warm-Up: Guess the Nationality</h2>
            <section className="bg-gray-900 rounded-lg border border-teal-800 p-4 mb-6">
                <p className="text-teal-700 mb-2">Lets start by guessing where these people are from! Read the sentence and think of the country.</p>
                <ul className="list-disc list-inside text-teal-700 space-y-2 mt-4">
                    <li><span className="font-medium text-teal-400">Olen suomalainen</span> (Im Finnish) </li>
                    <li><span className="font-medium text-teal-400">Hn on ranskalainen</span> (He/She is French) </li>
                    <li><span className="font-medium text-teal-400">Min olen italialainen</span> (Im Italian) </li>
                </ul>
                <p className="text-teal-600 italic mt-2">Try saying these out loud to get comfortable with the words!</p>
            </section>

            </PageWrapper>

            <PageWrapper title="2. Core Concepts: Becoming a Citizen">
                

            <section className="bg-gray-900 rounded-lg border border-teal-800 p-4 mb-6">
                <p className="text-teal-700 mb-4">In Finnish, you add <span className="font-medium">-lainen</span> or <span className="font-medium">-linen</span> to a countrys name to say your nationality. Lets see how it works!</p>

                {/* <!-- Citizens of a Country --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">a. Citizens of a Country</h3>
                    <p className="text-teal-700">Take a countrys name (always with a big letter!) and add <span className="font-medium">-lainen</span> or <span className="font-medium">-linen</span> to make the nationality.</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Suomi</span> (Finland)  <span className="font-medium text-teal-400">suomalainen</span> (Finnish)</li>
                        <li><span className="font-medium text-teal-400">Ranska</span> (France)  <span className="font-medium text-teal-400">ranskalainen</span> (French)</li>
                        <li><span className="font-medium text-teal-400">Japani</span> (Japan)  <span className="font-medium text-teal-400">japanilainen</span> (Japanese)</li>
                        <li><span className="font-medium text-teal-400">Sveitsi</span> (Switzerland)  <span className="font-medium text-teal-400">sveitsilinen</span> (Swiss)</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Quick Rule:</strong> Use <span className="font-medium">-lainen</span> for countries with a, o, u (like <span className="font-medium">Suomi</span>), and <span className="font-medium">-linen</span> for , , y (like <span className="font-medium">Sveitsi</span>)!</p>
                </div>

                {/* <!-- Asking and Answering --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">b. Asking and Answering</h3>
                    <p className="text-teal-700">Ask <span className="font-medium">Minkmaalainen sin olet?</span> (What nationality are you?) and answer with <span className="font-medium">olen</span> (I am) or <span className="font-medium">hn on</span> (he/she is)!</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Minkmaalainen sin olet?</span> (What nationality are you?)  
                        </li>
                        <li><span className="font-medium text-teal-400">Olen suomalainen</span> (Im Finnish).</li>
                        <li><span className="font-medium text-teal-400">Minkmaalainen hn on?</span> (What nationality is he/she?)  
                        </li>
                        <li><span className="font-medium text-teal-400">Hn on kiinalainen</span> (He/She is Chinese).</li>
                        <li><span className="font-medium text-teal-400">Mist sin olet kotoisin?</span> (Where are you from?)  
                        </li>
                        <li><span className="font-medium text-teal-400">Olen kotoisin Suomesta</span> (Im from Finland).</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Quick Rule:</strong> <span className="font-medium">Minkmaalainen</span> asks about nationality, <span className="font-medium">kotoisin</span> asks about origin with <span className="font-medium">-sta/-st</span>!</p>
                </div>

                {/* <!-- Using as Adjectives --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">c. Describing Things</h3>
                    <p className="text-teal-700">Nationality words can also describe things like food, cars, or friends!</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Syn italialaista ruokaa</span> (I eat Italian food).</li>
                        <li><span className="font-medium text-teal-400">Minulla on saksalainen auto</span> (I have a German car).</li>
                        <li><span className="font-medium text-teal-400">Minulla on ranskalainen ystv</span> (I have a French friend).</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Quick Rule:</strong> Add <span className="font-medium">-a</span> or <span className="font-medium">-</span> to the end when talking about some things (like food)!</p>
                </div>

                {/* <!-- More Examples --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">d. More Examples</h3>
                    <p className="text-teal-700">Here are extra examples to see nationalities in action!</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Suomalaiset ovat hiljaisia</span> (Finns are quiet).</li>
                        <li><span className="font-medium text-teal-400">Tanskalaiset voittivat</span> (The Danes won).</li>
                        <li><span className="font-medium text-teal-400">Juon ranskalaista viini</span> (I drink French wine).</li>
                        <li><span className="font-medium text-teal-400">Tykkn thaimaalaisesta ruoasta</span> (I like Thai food).</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Quick Rule:</strong> Add <span className="font-medium">-t</span> for plural (like <span className="font-medium">suomalaiset</span>) when talking about groups!</p>
                </div>
            </section>

            </PageWrapper>
            <PageWrapper title="3. Tips for Learning Nationalities">
                
            <section className="bg-gray-900 rounded-lg border border-teal-800 p-4 mb-6">
                <p className="text-teal-700 mb-2">Here are some helpful tips to make nationalities easy!</p>
                <ul className="list-disc list-inside text-teal-700 space-y-2">
                    <li><strong>Start with Your Country:</strong> Learn your own nationality first! If youre from <span className="font-medium text-teal-400">Intia</span> (India), say <span className="font-medium text-teal-400">Olen intialainen</span> (Im Indian).</li>
                    <li><strong>Vowel Harmony:</strong> Listen to the countrys vowels: <span className="font-medium">Suomi</span> (Finland) has u and o, so its <span className="font-medium">suomalainen</span>. <span className="font-medium">Sveitsi</span> (Switzerland) has e and i, so its <span className="font-medium">sveitsilinen</span>.</li>
                    <li><strong>Small Changes:</strong> Some countries tweak a bit: <span className="font-medium">Suomi</span> (Finland)  <span className="font-medium">suomalainen</span> (Finnish), not *suomilainen*. Watch for these!</li>
                    <li><strong>Practice with Food:</strong> Pair nationalities with food you like: <span className="font-medium">Syn kiinalaista ruokaa</span> (I eat Chinese food) or <span className="font-medium">Juon espanjalaista viini</span> (I drink Spanish wine).</li>
                    <li><strong>Ask Friends:</strong> Try <span className="font-medium">Minkmaalainen sin olet?</span> (What nationality are you?) with people you knowits a great conversation starter!</li>
                </ul>
            </section>

            </PageWrapper>
            <PageWrapper title="4. More Info: Special Cases">
            <section className="bg-gray-900 rounded-lg border border-teal-800 p-4 mb-6">
                <p className="text-teal-700 mb-2">Some nationalities have tricks to know!</p>

                {/* <!-- Exceptional Cases --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">a. Exceptional Cases</h3>
                    <p className="text-teal-700">A few countries change slightly before adding <span className="font-medium">-lainen</span>:</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Suomi</span> (Finland)  <span className="font-medium text-teal-400">suomalainen</span> (Finnish)  The i becomes u!</li>
                        <li><span className="font-medium text-teal-400">Ruotsi</span> (Sweden)  <span className="font-medium text-teal-400">ruotsalainen</span> (Swedish)  Drops the i!</li>
                        <li><span className="font-medium text-teal-400">Venj</span> (Russia)  <span className="font-medium text-teal-400">venlinen</span> (Russian)   stays, but j goes!</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Tip:</strong> Dont worry too muchjust memorize these common ones!</p>
                </div>

                {/* <!-- United States --> */}
                <div className=" p-4 rbg-gray-900 rounded-lg border border-teal-800 mb-4">
                    <h3 className="text-xl font-medium text-teal-700 mb-2">b. Special Case: The United States</h3>
                    <p className="text-teal-700">The U.S. has two main names in Finnish:</p>
                    <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                        <li><span className="font-medium text-teal-400">Yhdysvallat</span> (United States)  <span className="font-medium text-teal-400">yhdysvaltalainen</span> (American)  The proper way!</li>
                        <li><span className="font-medium text-teal-400">Amerikka</span> (America)  <span className="font-medium text-teal-400">amerikkalainen</span> (American)  More casual and common!</li>
                    </ul>
                    <p className="text-teal-600 mt-2"><strong>Tip:</strong> Use <span className="font-medium">amerikkalainen</span> in everyday talkits easier!</p>
                </div>
            </section>


            </PageWrapper>
            <PageWrapper title="5. Wrap-Up: Your Nationality Kit">
                


            <section className="bg-gray-900 rounded-lg border border-teal-800 p-4 mb-6">
                <p className="text-teal-700 mb-2">Youre ready to talk about nationalities! Heres your starter kit:</p>
                <ul className="list-disc list-inside text-teal-700 space-y-1 mt-4">
                    <li><span className="font-medium text-teal-400">Minkmaalainen sin olet?</span> (What nationality are you?)  <span className="font-medium text-teal-400">Olen suomalainen</span> (Im Finnish).</li>
                    <li><span className="font-medium text-teal-400">Minulla on ranskalainen ystv</span> (I have a French friend).</li>
                    <li><span className="font-medium text-teal-400">Syn espanjalaista ruokaa</span> (I eat Spanish food).</li>
                </ul>
                <p className="text-teal-600 font-bold mt-4"><span className="font-medium">Hyv!</span> (Good!)  Practice these every day to get confident!</p>
            

            </section>

            <Link to={`/beginars/nationality/what-nationality-are-you/nationality-sentence-quiz`}>
                        <button className="mt-4 bg-teal-900 text-white px-4 py-2 rounded hover:bg-teal-600 transform hover:scale-110 transition duration-200 m-2">
                        Nationality Exercises
                        </button>
                    </Link>

                

            </PageWrapper>  

                    
        </div>
    </BodyWrapper>

                    );

            }


            export default NationalityPage;